class CountSlider {
	constructor(bl, time = 6000){
		this.bl = $(bl);
		this.slide_images = this.bl.find('.img');
		this.img_traffic = this.slide_images.find('.img-traffic')
		this.slide_nums = this.bl.find('.num');
		this.time = time;
		this.max = this.slide_images.length;
		this.count = 0;
		this.init()
	}
	init(){
		this.addAnimationDuration()
		this.startSlide(this.count)
	}
	addAnimationDuration(){
		this.slide_images.css('animation-duration', this.time+'ms')
		this.slide_nums.css('animation-duration', this.time+'ms')
		this.img_traffic.css('animation-duration', this.time+'ms')
	}
	startSlide(count){
		let img = this.slide_images.eq(count)
		let num = this.slide_nums.eq(count)
		this.changeActiveClass(img, num)
	}
	changeActiveClass(img, num){
		let time = this.time
		let time_remove = time + 1000
		this.addActiveClass(img, num)
		setTimeout(()=>{
			num.countTo('restart');
		},500)
		setTimeout(()=>{
			this.changeCount()
			this.startSlide(this.count)
		},time)
		setTimeout(()=>{
			this.removeActiveClass(img, num)
		},time_remove)
	}
	changeCount(){
		this.count = this.count +1;
		if(this.count >= this.max){
			this.count = 0
		}
	}
	addActiveClass(img, num){
		img.addClass('active')
		num.addClass('active')
	}
	removeActiveClass(img, num){
		img.removeClass('active')
		num.removeClass('active').text('0')
	}
}
export default CountSlider