export function placeholderSlide (el) {
  $(el).on('focus', function () {
    $(this).addClass('active');
  });
  $(el).on('blur', function () {
    if (!$(this).val()) {
      $(this).removeClass('active');
    }
  });

}

export function passwordChange (el) {
 
  $(el).on('change', function () {
    let input = $(this).closest('.password').find('.form__input');
    if ($(this).is(":checked")) {
      $(input).attr("type", "text");
    } else {
      $(input).attr("type", "password");
    }
  });
}