function afterScroll() {
  lottie.play();
}

function scrollingTo(el, callback) {
  if (el.hash !== "") {
    let hash = el.hash;
    let top = $(hash).offset().top;
    $('html, body').stop().animate({
      scrollTop: top
    }, 800, callback);
  }
};

// ------------------------------

export function linkClick(links) {
  $(links).on('click', function (event) {
    event.preventDefault();
    lottie.stop();
    scrollingTo(this, afterScroll);
  })
}
// --------------------------------------

export function menuDropDown(el) {
  $(el).hover(function () {
      $(this).find('.drop-down__content').stop().slideDown(300);
      $(this).addClass("active");
    },
    function () {
      $(this).find('.drop-down__content').stop().slideUp(300);
      $(this).removeClass("active");
    });
}

// ------------------------------

export function burgerToggle(burger, navMobJs) {
  $(burger).on("click", (function () {
    $(this).toggleClass("open");
    $(navMobJs).stop().slideToggle(300);
  }));
}

export function mobLinkClick(burger, navMobJs, links) {
  $(links).on('click', function (event) {
    event.preventDefault();
    $(burger).removeClass("open");
    $(this).closest(navMobJs).slideUp(300);
    lottie.stop();
    scrollingTo(this, afterScroll);
  })
}

export function documentScroll(logo, floatingButton) {
  if ($(document).scrollTop() > 0) {
    $(logo).addClass("logo_min");
    $(floatingButton).addClass("active");
  } else {
    $(logo).removeClass("logo_min");
    $(floatingButton).removeClass("active");
  }
}

export function floatingButtonClick(floatingButton) {
  $(floatingButton).on('click', function () {
    lottie.stop();
    $('html, body').stop().animate({
      scrollTop: 0
    }, 800, function () {
      lottie.play();
    });
  })
}