export function slideUpInfinity(section) {
  let i = 0,
    n = $(section).length - 1,
    sectionSlideUpInterval;

  function sectionSlideUp() {
    $(section).removeClass('hover-active active');
    $(section).eq(i).addClass('active');
    if (i < n) {
      i++;
    } else {
      i = 0;
    }
  }

  sectionSlideUpInterval = setInterval(sectionSlideUp, 5000);
  $(section).hover(
    function () {
      clearInterval(sectionSlideUpInterval);
      $(section).removeClass('hover-active active');
      $(this).addClass('hover-active');
    },
    function () {
      $(section).removeClass('hover-active active');
      sectionSlideUpInterval = setInterval(sectionSlideUp, 5000);
    });

}
