class InWindow {
  constructor(el) {
    this.scrollTop = $(window).scrollTop();
    this.windowHeight = $(window).height();
    this.currentElements = el;
    this.result = [];
    this.offset = 0;
    this.height = 0;
  }
  updatePar() {
    this.result = [];
    this.scrollTop = $(window).scrollTop();
    this.windowHeight = $(window).height();
  }
  elInWindowAll() {
    this.updatePar();
    this.currentElements.each((i, element) => {
      this.offset = $(element).offset().top;
      this.height = $(element).outerHeight();
      if (this.scrollTop <= this.offset && (this.height + this.offset) < (this.scrollTop + this.windowHeight)) {
        this.result.push(element)
      }
    });
    return this.result;
  }
  elInWindowTop() {
    this.updatePar();
    this.currentElements.each((i, element) => {
      this.offset = $(element).offset().top;
      if (this.scrollTop <= this.offset && this.offset < (this.scrollTop + this.windowHeight)) {
        this.result.push(element);
      }
    });
    return this.result;
  }
}

// ---------------------------------------------

let active = $("#menuJs").find(".active"),
  visibleSection,
  id,
  newActive;

let inWindow = new InWindow($(".visibleSection"));

export function menuItemAvtive(menu) {
  visibleSection = inWindow.elInWindowTop()[0];
  if (visibleSection) {
    id = $(visibleSection).attr("id");
    if ($(active).attr("href") == "#" + id) {
      return;
    } else {
      newActive = `a[href='#${id}']`;
      $(active).removeClass("active");
      $(menu).find(newActive).addClass("active");
      history.replaceState(null, null, window.location.pathname + "#" + id);
      active = $(newActive);
    }
  }
}

