// Main JS module
// objectFitImages polyfill
import objectFitImages from "object-fit-images";

import * as sectionEffects from "./modules/sectionEffects.js";
import * as form from "./modules/form.js";
import * as moduleScrollingTo from "./modules/moduleScrollingTo.js";
import * as moduleInWindow from "./modules/moduleInWindow.js";

// import AOS from 'aos';
// import lottie from 'lottie-web';
import slick from "slick-carousel";
// import chosen from "chosen-js";

import CountSlider from "./modules/CountSlider.js";

import main from "./json/main.json";
import logistik from "./json/logistik.json";
import nalojka from "./json/nalojka.json";
import vat from "./json/vat.json";
import legal from "./json/legal.json";
import product from "./json/product.json";
import sms from "./json/sms.json";
import api from "./json/api.json";
import api_mob from "./json/api_mob.json";
import lab_laboratory from "./json/lab_laboratory.json";
import lab_design from "./json/lab_design.json";
import velik from "./json/velik.json";

$(function () {
  objectFitImages();

  // ---------------------------------------------
  // lottie

  lottie.loadAnimation({
    container: document.getElementById('main_anim'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: main
  });

  lottie.loadAnimation({
    container: document.getElementById('logistik'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: logistik
  });

  lottie.loadAnimation({
    container: document.getElementById('nalojka'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: nalojka
  });

  lottie.loadAnimation({
    container: document.getElementById('vat'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: vat
  });

  lottie.loadAnimation({
    container: document.getElementById('legal'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: legal
  });

  lottie.loadAnimation({
    container: document.getElementById('product'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: product
  });

  lottie.loadAnimation({
    container: document.getElementById('sms'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: sms
  });



  lottie.loadAnimation({
    container: document.getElementById('lab_laboratory'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: lab_laboratory
  });

  lottie.loadAnimation({
    container: document.getElementById('lab_design'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: lab_design
  });

  lottie.loadAnimation({
    container: document.getElementById('velik'),
    renderer: 'svg',
    loop: true,
    autoplay: false,
    animationData: velik
  });

  setTimeout(function () {
    $('#loader').fadeOut(300);
  }, 300);

  lottie.play();

  // ---------------------------------------------

  let
    links = $('.linkJs'),
    menu = $("#menuJs"),

    inputPH = $('.placeholder-container .form__input'),
    passwordCheckbox = $('.password .checkbox');

  // -----------------------------------------------

  form.placeholderSlide(inputPH);
  form.passwordChange(passwordCheckbox);

  moduleInWindow.menuItemAvtive(menu);

  $(window).on('scroll', (function () {
    // menu Item Avtive
    moduleInWindow.menuItemAvtive(menu);

  }));

  //*************** Count slider ***************
  let count_sl = $('.count-slider')
  if (count_sl.length) {
    for (let i = 0; i < count_sl.length; i++) new CountSlider(count_sl[i])
  }

  $(window).on('load', function () {

    let linkSection = document.querySelector('.section1__section-link');
    try {
      let linkSectionWidth = linkSection.clientWidth;
      let root = document.querySelector(':root');
      root.style.setProperty('--link-width', linkSectionWidth + 'px');
    } catch {

    }

  });

  function functionMedia(mediaQuery) {
    if (mediaQuery.matches) {
      let
        burger = $(".burger"),
        logo = $(".header-mob__logo"),
        floatingButton = $(".floating-button"),
        navMobJs = $("#navMobJs");

      moduleScrollingTo.mobLinkClick(burger, navMobJs, links);
      moduleScrollingTo.burgerToggle(burger, navMobJs);

      moduleScrollingTo.floatingButtonClick(floatingButton);

      moduleScrollingTo.documentScroll(logo, floatingButton);
      $(window).on('scroll', (function () {
        moduleScrollingTo.documentScroll(logo, floatingButton);
      }));

      // -----------------------------------------------
      let
        slider = $(".section1__sections"),
        mainImg = $("#main_anim");

      $(slider).slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        // initialSlide: 1,
        arrows: false,
        dots: true,
        infinite: true
      });

      $(slider).on('beforeChange', function (event, slick, currentSlide, nextSlide) {
        $(mainImg).removeClass("section1__img_" + currentSlide);
        $(mainImg).addClass("section1__img_" + nextSlide);
      });

      // -----------------------------------------------

      let api_mob_anim = lottie.loadAnimation({
        container: document.getElementById('api'),
        renderer: 'svg',
        loop: true,
        autoplay: false,
        animationData: api_mob
      });

      api_mob_anim.play();

    } else {
      let
        section = $('.section1__section'),
        dropDown = $(".drop-down");

      moduleScrollingTo.linkClick(links);

      sectionEffects.slideUpInfinity(section);

      moduleScrollingTo.menuDropDown(dropDown);

      // -----------------------------------------------

      let api_anim = lottie.loadAnimation({
        container: document.getElementById('api'),
        renderer: 'svg',
        loop: true,
        autoplay: false,
        animationData: api
      });

      api_anim.play();
    }

  }
  let mediaQuery = window.matchMedia("(max-width: 1140px)");
  functionMedia(mediaQuery);
  mediaQuery.addListener(functionMedia);

});